<template>
  <div class="consejo">
    <h3 :class="['frase', escena]">
      Anda, cógete <b>{{objetos[icon_ppal]}}</b><br>
      <span v-html="frases[tiempo.main][Math.floor(Math.random() * frases[tiempo.main].length)]"></span>
    </h3>
    <div class="iconos" :class="{'on': escena === 'e_iconos'}">
      <img :class="['mano_slide', icon_ppal]" :src="'img/brazo_abu.svg'" />
      <img class="icono_ppal" :src="'img/iconos/' + icon_ppal + '.svg'" />
      <span class="sombra_icono"></span>
      <div class="otros_consejos">
        <img v-for ="(icono, index) in other_icons" :key="index"
          :alt="icono" :src="'img/iconos/' + icono + '.svg'">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Consejo',
  components: {},
  props: {
    tiempo: {}
  },
  data() {
    return {
      icon_ppal: null,
      other_icons: [],
      escena: 'e_frase',
      appear: false,
      objetos: {
        abrigo: 'el abrigo',
        botas_lluvia: 'unas botas para la lluvia',
        botella_agua: 'una botellita de agua',
        gafas_sol: 'las gafas de sol',
        gorro_nieve: 'un gorrito para la nieve',
        guantes_nieve: 'guantes para la nieve',
        lazo_viento: 'un lazo para el pelo',
        paraguas_nieve: 'un paraguas para la nieve',
        paraguas: 'el paraguas',
        rebeca: 'una rebeca',
        sandalias: 'las sandalias',
        sudadera: 'una sudadera'
      },
      frases: {
        Snow: ['que cuando en marzo mayea<br>en mayo marcea'],
        Drizzle: ['que cuando en marzo mayea<br>en mayo marcea'],
        Thunderstorm: ['que cuando en marzo mayea<br>en mayo marcea'],
        Rain: ['que cuando en marzo mayea<br>en mayo marcea'],
        Atmosphere: ['que cuando en marzo mayea<br>en mayo marcea'],
        Clear: ['que cuando en marzo mayea<br>en mayo marcea'],
        Clouds: ['que cuando en marzo mayea<br>en mayo marcea']
      },
    }
  },
  created: function() {
    if (this.tiempo) {
      if (this.tiempo.lluvia > .15) this.icon_ppal = 'paraguas';
      else if (this.tiempo.temperatura < 10) this.icon_ppal = 'abrigo';
      else if (this.tiempo.temperatura < 15) this.icon_ppal = 'sudadera';
      else if (this.tiempo.temperatura < 22) this.icon_ppal = 'rebeca';
      else if (this.tiempo.nubes < 5) this.icon_ppal = 'gafas_sol';
      else if (this.tiempo.viento > 1) this.icon_ppal = 'lazo_viento';
      else if (this.tiempo.temperatura >= 23) this.icon_ppal = 'sandalias';
    }
  },
  mounted: function() {
    //   tiempo: {
    //     temperatura: null,
    //     nubes: null,
    //     lluvia: null,
    //     viento: null
    //   }
    this.$nextTick(() => {
      setTimeout(() => {
        this.escena = 'e_iconos';
      }, 2000);
    })
  },
  methods: {
  }
}
</script>
