  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: space-evenly;
  .frase {
    b {font-weight: bold;}
  }
  // .frase {

  //   &:not(.e_frase) {
      
  //   }
  // }
  .iconos {
    position: relative;
    width: 60%;
    height: auto;
    margin-top: 10%;
    .icono_ppal {
      position: relative;
      width: 100%;
      display: block;
      z-index: 10;
      left: -75vw;
      // transition: left .5s ease-out;
    }
    .mano_slide {
      position: absolute;
      width: 100%;
      z-index: 11;
      left: -100vw;
      &.paraguas {top: 70%;}
    }
    &.on .icono_ppal {animation: icono_ppal 3s ease-out forwards;}
    &.on .mano_slide {animation: 1 brazo_abu 3s ease-out;}
   
    .sombra_icono {
      position: absolute;
      top: -15%;
      left: -15%;
      width: 130%;
      height: 130%;
      border-radius: 100%;
      background: rgba(255, 255, 255, 0.7);
      mix-blend-mode: overlay;
      filter: blur(20px);
      z-index: 0;
      opacity: 0;
      transition: opacity .5s ease-in-out .5s;
    }
    &.on .sombra_icono {opacity: 1;}
  }
}
</style>
